export function getColHeaderHeightIndex(node) {
  return node.level + node.rowSpan - 2
}

let scrollBarWidth;
export function getScrollBarWidth() {
  if (scrollBarWidth !== undefined) return scrollBarWidth;
  const dom = document.createElement('div');
  const body = document.body || dom;

  dom.style.visibility = 'hidden';
  dom.style.width = '100px';
  dom.style.position = 'absolute';
  dom.style.top = '-9999px';
  dom.style.overflow = 'scroll';

  body.appendChild(dom);

  const totalWidth = dom.offsetWidth;
  const widthWithoutScroll = dom.clientWidth;

  body.removeChild(dom);

  return totalWidth - widthWithoutScroll;
}

export function convertToColumnHeader(columns = []) {
  let maxLevel = 1;

  function traverse(column, parent) {
    if (parent) {
      column.level = parent.level + 1;
      if (maxLevel < column.level) {
        maxLevel = column.level;
      }
    } else {
      column.level = 1;
    }

    if (column.children.length) {
      let colSpan = 0;
      column.children.forEach((subColumn) => {
        traverse(subColumn, column);
        colSpan += subColumn.colSpan;
      });
      column.colSpan = colSpan;
    } else {
      column.colSpan = 1;
    }
  }

  columns.forEach((column) => {
    traverse(column);
  });

  const rows = [];
  for (let i = 0; i < maxLevel; i++) {
    rows.push([]);
  }

  const allColumns = [];
  const queue = columns.slice();
  for (let i = 0; queue[i]; i++) {
    allColumns.push(queue[i]);
    if (queue[i].children.length) queue.push(...queue[i].children);
  }

  allColumns.forEach((column) => {
    if (!column.children.length) {
      column.rowSpan = maxLevel - column.level + 1;
    } else {
      column.rowSpan = 1;
    }
    rows[column.level - 1].push(column);
  });
  return rows;
  
}

export function convertToRowHeader(columns = []) {
  let maxLevel = 1;
  let rowsLength = 0;

  function traverse(column, parent) {
    if (parent) {
      column.level = parent.level + 1;
      if (maxLevel < column.level) {
        maxLevel = column.level;
      }
    } else {
      column.level = 1;
    }

    //  row span  
    if (column.children.length > 0) {
      let rowSpan = 0;
      column.children.forEach((child) => {
        traverse(child, column);
        rowSpan += child.rowSpan;
      });
      column.rowSpan = rowSpan;
    } else {
      column.rowSpan = 1;
      return rowsLength++;
    }
  }

  columns.forEach((column) => {
    traverse(column);
  });

  let rows = []
  for(let i = 0; i < rowsLength; i++) {
    rows.push([]);
  };

  //  index
  const posCount = {};
  for(let i = 1; i <= maxLevel; i++) {
    posCount[i] = 0;
  }

  const allColumns = [];
  const queue = columns.slice();
  for (let i = 0; queue[i]; i++) {
    allColumns.push(queue[i]);
    if (queue[i].children) queue.push(...queue[i].children);
  }

  //  col span, 
  allColumns.forEach((column) => {
    if (column.children.length === 0) {
      column.colSpan = maxLevel - column.level + 1;
    } else {
      column.colSpan = 1;
    }
  });


  allColumns.forEach(column => {
    const { rowSpan, level } = column;
    //   offset
    let offset = 0; 
    column.start = posCount[level];
    if (column.parent && column.parent.start > column.start) {
      offset += column.parent.start - column.start;
      column.start += offset;
    }
    posCount[level] += offset;
    rows[posCount[level]].push(column);
    posCount[level] += rowSpan;
  });
  return rows;
}

export function getLeafColumns(columns) {
  const result = [];
  columns.forEach((column) => {
    if (column.children) {
      result.push(...getLeafColumns(column.children));
    } else {
      result.push(column);
    }
  });
  return result;
}

let ascId = 0;
export function getAscId(){
  return ascId++;
}

let matchesFun = '';
function isMatchSelector(el, selector) {
  if (!matchesFun) {
    [
      'matches',
      'webkitMatchesSelector',
      'mozMatchesSelector',
      'msMatchesSelector',
      'oMatchesSelector',
    ].forEach(f => {
      if (typeof el[f] === 'function') {
        matchesFun = f;
      }
    });
  }
  return el[matchesFun](selector);
}

export function matchsSlelector(el, selector, rootDom) {
  let node = el;
  while(node) {
    if (isMatchSelector(el, selector)) return true;
    if (node === rootDom) return false;
    node = node.parentNode;
  }
  return false;
}

export const isNumber = val => typeof val === 'number';
